import keyboader from "../../components/keyboader/keyboader";
import { IndexModel } from '../../model/index';
import { setTimeout } from 'timers';
const indexModel = new IndexModel();
export default {
    components: {keyboader },
    data() {
        return {
            car_num:'',
            phone:'',
            contacts:'fds', 
            checked:[],
            show_keyboader:false,
        }
    },
    methods: {
      openKeyboard(){
          console.log("open")
        this.show_keyboader=true
      },
      closeKeyboard(){
          console.log("close")
        this.show_keyboader=false
      },
      changeValue(e){
         this.car_num=e
      },
      contactsInput(event){
        //   console.log(event);
        let contacts = event.target.value;
        if(!(/^[\u4e00-\u9fa5]{0,}$/).test()) {
            uni.showToast({
                title: '请输入汉字',
                icon: 'none'
            });
        }else {
            this.contacts = contacts;
        }
      },
      phoneInput(event){
          let phone = event.target.value;
          if(!(/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/).test(phone)) {
              uni.showToast({
                title: '请输入正确的电话号',
                icon: 'none'
            });
          }else {
              this.phone = phone;
          }
      },
      submit() {
        let params = {
            contacts:this.contacts,
            car_num:this.car_num,
            commodity_ids:this.checked,
            phone:this.phone
        }
        indexModel.delayApply(params).then((res) => {
             uni.showToast({
                 title:res.message,
                 icon:'none'
             })
             if (res.code == 201) {
                 setTimeout(()=>{
                     uni.navigateTo({
                        url: `/pagesA/delay_submit/delay_submit`
                    });
                 },200)
             }
        })


        
      }
    },
    onLoad(options){
        this.checked = options.checked
    }
}
